<template>
  <section class="grid-lab">
    <article
      v-for="(item, article) in design"
      :key="article"
      @click="showModal(item.title, item.description, item.screen, item.url)"
    >
      <figure>
        <img :src="item.screen" title="Abrir modal" :alt="item.screen" />
      </figure>
      <div>
        <h2>{{ item.title }}</h2>
      </div>
    </article>
    <section class="modal" v-show="modal">
      <div class="modal-block">
        <figure>
          <img class="modal-image" />
        </figure>
        <div class="modal-info">
          <h2 class="modal-title"></h2>
          <p class="modal-description"></p>
        </div>
        <div class="modal-buttons">
          <a id="modal-link" target="_blank" class="btn btn-primary"
            ><i class="fas fa-external-link-alt"></i> Ver projeto</a
          >
          <a @click="modal = false" class="btn btn-secondary"
            ><i class="fas fa-times"></i> Fechar</a
          >
        </div>
      </div>
    </section>
  </section>
</template>


<script>
export default {
  name: "Lab",
  data() {
    return {
      modal: false,
      design: [
        {
          title: "renato7oliveira",
          description: "Wireframe deste portfolio em XD.",
          url: "https://www.dropbox.com/s/c5p5d6l4j8hge5x/RENATO7OLIVEIRA-PORTFOLIO.xd?dl=0",
          screen: require("../assets/images/portfolio-design-ui-renato7oliveira.jpg"),
        },
      ],
    };
  },
  methods: {
    showModal(modalTitle, modalDescription, modalImage, modalUrl) {
      this.modal = true;
      document.querySelector(".modal-image").src = modalImage;
      document.querySelector(".modal-image").setAttribute("alt", modalTitle);
      document.querySelector(".modal-image").setAttribute("title", modalTitle);
      document.querySelector(".modal-title").innerHTML = modalTitle;
      document.querySelector(".modal-description").innerHTML = modalDescription;
      document.querySelector("#modal-link").setAttribute("href", modalUrl);
    },
  },
};
</script>